import { IIdentified, IManagedObject } from '@c8y/client';

export abstract class Ng1UserInventoryRolesService {
  abstract listRootGroups({ onlyRootsUser }): Promise<IIdentified[]>;
  abstract flatTree(tree, depth?, list?): IIdentified[];
  abstract loadChildren(mo: IManagedObject): Promise<IManagedObject[]>;
  abstract expandAll(mos: IManagedObject[], callback: () => void);
}

export function UserInventoryRolesFactory(injector) {
  return injector.get('c8yUserInventoryRoles');
}

export const userInventoryRolesServiceProvider = {
  provide: Ng1UserInventoryRolesService,
  useFactory: UserInventoryRolesFactory,
  deps: ['$injector']
};
