import { IManagedObject } from '@c8y/client';

export abstract class Ng1RolesService {
  abstract list(): Promise<IManagedObject>;
}

export function RolesServiceFactory(injector) {
  return injector.get('c8yRoles');
}

export const rolesServiceProvider = {
  provide: Ng1RolesService,
  useFactory: RolesServiceFactory,
  deps: ['$injector']
};
